* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: sans-serif;
}

html,
body {
  height: 100%;
  width: 100%;
}

/* Weather container centered on screen */
#weatherDiv {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: min(90vw, 600px);
  background-color: rgba(0, 0, 0, 0.4); /* fallback if blur isn't supported */
  backdrop-filter: blur(12px) brightness(0.8);
  border-radius: 1rem;
  padding: 2rem;
  color: white;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

/* Location at the top */
.locationDiv {
  text-align: center;
  font-size: 2rem;
  font-weight: bold;
}

/* First row: current temp + icon */
.firstDataDiv {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* Left part: temperature */
.tempDiv {
  display: flex;
  align-items: center;
  gap: 1rem;
}

/* Big current temp */
.currentTemp {
  font-size: 3rem;
  font-weight: bold;
}

/* Right of temp: max/min and condition */
.tempRightDiv {
  display: flex;
  flex-direction: column;
  font-size: 0.9rem;
  gap: 0.25rem;
}

/* Weather icon */
.firstDataDiv i {
  font-size: 4rem;
}

/* Second row: descriptions */
.secondDataDiv {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.dailyDescriptionDiv {
  font-size: 1rem;
  font-style: italic;
}

.descriptionDiv {
  font-size: 0.9rem;
}

/* Third row: humidity and sun times */
.thirdDataDiv {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 1rem;
}

.humidityDiv,
.sunriseDiv,
.sunsetDiv {
  font-size: 0.9rem;
}

.formContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  gap: 10px;
}

.formContainer input {
  padding: 10px;
  font-size: 1rem;
  border-radius: 8px;
  border: none;
  width: 200px;
}

.formContainer button {
  padding: 10px 16px;
  font-size: 1rem;
  border: none;
  border-radius: 8px;
  background-color: rgba(255, 255, 255, 0.2);
  color: white;
  cursor: pointer;
  transition: background-color 0.3s;
}

.formContainer button:hover {
  background-color: rgba(255, 255, 255, 0.4);
}

.errorDiv {
  background-color: #ff6b6b;
  margin-top: 10px;
  font-size: 0.95rem;
  font-weight: bold;
  text-align: center;
  display: none;
}

.unitToggle {
  margin-left: 10px;
  padding: 8px 12px;
  border-radius: 8px;
  border: none;
  background-color: rgba(255, 255, 255, 0.2);
  color: white;
  cursor: pointer;
  transition: background-color 0.3s;
}

.unitToggle:hover {
  background-color: rgba(255, 255, 255, 0.4);
}
